	module.exports = {"data":{"markdownRemark":{"html":"<p>MQTT is a TCP protocol. TCP enables two hosts to establish a connection and exchange streams of data. This is different from UDP, as UDP is connectionless. MQTT was designed as an extremely lightweight publish/subscribe messaging transport from machine to machine. There is no message format for MQTT, you can send any type of message over MQTT. It is therefore designed for many different use cases.</p>\n<p>Examples of use cases include push instead of pull messaging and enterprise to mobile messaging. A concrete example of an enterprise to mobile use case is Facebook Messenger. Thomas also gave an example of Siberia where the Internet connection is established over satellites. This is a nice example of Internet-Of-Things.</p>","excerpt":"MQTT is a TCP protocol. TCP enables two hosts to establish a connection and exchange streams of data. This is different from UDP, as UDP is connectionless. MQTT was designed as an extremely lightweight publish/subscribe messaging transport from…","frontmatter":{"date":"March 09, 2018","path":"/blog/think-twice","title":"Think twice","featuredImage":{"childImageSharp":{"resolutions":{"base64":"data:image/jpeg;base64,","width":900,"height":300,"src":"/static/meetup-687bf10b679e8856fb32830f415f931f-f5803.jpg","srcSet":"/static/meetup-687bf10b679e8856fb32830f415f931f-f5803.jpg 1x"}}}}}},"pathContext":{}}

